"use client"

import { useSession } from "next-auth/react"
import { useRouter, usePathname } from "next/navigation"
import { useEffect } from "react"
import { Home, Calendar, Wallet, History, User } from "lucide-react"
import { BottomNav, PageLoader } from "@/components/ui"

const navItems = [
  { href: "/customer", label: "Home", icon: Home },
  { href: "/customer/pause", label: "Pause", icon: Calendar },
  { href: "/customer/wallet", label: "Wallet", icon: Wallet },
  { href: "/customer/history", label: "History", icon: History },
  { href: "/customer/profile", label: "Profile", icon: User },
]

export default function CustomerLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const { data: session, status } = useSession()
  const router = useRouter()
  const pathname = usePathname()

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login/customer")
    }
  }, [status, router])

  // Show loader while checking auth
  if (status === "loading") {
    return <PageLoader />
  }

  // If not authenticated, show loader (will redirect via useEffect)
  if (status === "unauthenticated") {
    return <PageLoader />
  }

  // If session exists but role isn't customer (shouldn't happen normally)
  if (session && session.user.role !== "customer") {
    // Redirect to appropriate dashboard based on role
    router.push(`/${session.user.role}`)
    return <PageLoader />
  }

  // Check if user needs to complete registration
  const isRegisterPage = pathname === "/customer/register"
  if (session?.user.status === "NEW" && !isRegisterPage) {
    router.push("/customer/register")
    return <PageLoader />
  }

  // For register page, don't show bottom nav
  if (isRegisterPage) {
    return children
  }

  return (
    <div className="min-h-screen bg-background pb-20">
      {children}
      <BottomNav items={navItems} />
    </div>
  )
}
